.profile__name {
    max-width: 294px;
    
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    color: #FFFFFF;

    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  
    grid-area: name;
    margin: 0;
  }
  
@media screen and (max-width: 770px) {
  .profile__name {
    max-width: 189px;
    
    font-size: 27px;
    line-height: 33px;
    text-align: center;
  }
}